.navbar {
  background: rgb(30,30,30);
  height: 5rem;
  max-height: 75rem;
  width: 100vw;
  margin: 0 auto;
  padding: 1rem 2rem ;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 3; 
}

.menu{
  display: flex;
  flex-direction: row;
}

.menuListContact{
  display: flex;
  flex-direction: row;
}

.menuListItem {
  margin: 1rem;
  cursor: pointer;
  color: white;
}

.menuListItem:hover{
  color: #6c63ff;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid #6c63ff;
}

@media screen and (max-width: 840px){
  
  .navbar {
    background: rgb(30,30,30);
    height: 9rem;
    max-height: 75rem;
    width: 100vw;
    margin: 0 auto;
    padding: 1rem 2rem ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3; 
  }

  .menu{
    display: flex;
    flex-direction: row;
  }
  
}

